    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    vertical-align: middle;
}
.work ul li table {
    width: 300px
}
.work_inner {
    width: 100%;
    height: 100%;
    position: absolute;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 1;
    pointer-events: auto;
    transition: all .5s;
    -webkit-transition: all .5s;
    -moz-transition: all .5s
}
.work ul li:hover .work_inner {
    opacity: 0;
    pointer-events: none
}
.work ul li:hover .work_inner_option {
    opacity: 1;
    pointer-events: auto
}
.work_inner_option {
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0;
    pointer-events: none;
    transition: all .5s;
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    background-color: rgba(0, 0, 0, 0.3)
}
.work_inner_option h1 {
    font-size: 34px;
    line-height: 34px;
    width: 100%;
    margin: 70px 0 0 0;
    padding-left: 10%;
    text-transform: uppercase;
    transition: all .5s;
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    text-align: left
}
.work_inner_option p {
    font-size: 16px;
    line-height: 40px;
    width: 100%;
    margin: 20px 0 60px;
    padding-left: 10%;
    text-transform: uppercase;
    transition: all .5s;
    -webkit-transition: all .5s;
    -moz-transition: all .5s
}
.work .work_inner_option table {
    margin: auto;
    width: auto
}
.btn_outer {
    width: 100%
}
.work_logo_div,
.work_slogan_div {
    position: absolute;
    width: 100%;
    height: 100%
}
.work_btn {
    width: 100%;
    padding-left: 10%;
    height: 50px;
    line-height: 50px;
    display: inline-block;
    transition: all .5s;
    -webkit-transition: all .5s;
    -moz-transition: all .5s
}
.work_btn_img {
    width: 50px;
    height: 50px;
    float: left;
    background-position: center;
    background-repeat: no-repeat;
    transition: all .5s;
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    background-size: 60%
}
.take_me_there {
    background-image: url(../images/take_me_there.png)
}
.case_study {
    background-image: url(../images/case_study.png)
}
.work_btn_text {
    width: 100%;
    height: 50px;
    line-height: 50px;
    float: left;
    text-transform: capitalize;
    transition: all .5s;
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    font-style: italic;
    font-size: 20px;
    margin-top: 20px;
    position: relative
}
.work_btn_text::after {
    content: "";
    position: absolute;
    height: 55px;
    width: 55px;
    left: 160px;
    background-image: url(../images/right.png);
    background-position: center;
    background-repeat: no-repeat;
    transition: all .5s;
    -webkit-transition: all .5s;
    -moz-transition: all .5s
}


.work_btn_text:hover::after {
    left: 170px
}
