.blog-sidebar h4{ font-size: 25px; padding-bottom: 50px; color: #333; }
.blog-sidebar img{ padding-bottom: 0; }
.blog-sidebar p{ margin: 0 !important; text-align: left; width: 100% !important; padding-left: 5px; }

.prev-next{ width: 50%; display: inline-block; vertical-align: top; }
.prev-next .table-block{display: table; width: 100%; height: 150px; background-repeat: no-repeat; background-size: cover;  }
.prev-next .table-cell{display: table-cell; text-align: center; vertical-align: middle; background: rgba(0,0,0,0.2); position: relative; transition: all .5s;-webkit-transition: all .5s; -moz-transition: all .5s }
.prev-next:hover .table-cell{background: rgba(0,0,0,0.5); }
.next-blog{ padding-left: 15px; }
.prev-blog{ padding-right: 15px; }
.prev-next img{ position: absolute; width: 15px; top: 50%; margin-top: -15px; padding-bottom: 0; opacity: 0; transition: all .5s;-webkit-transition: all .5s; -moz-transition: all .5s }
.next-blog img{ right: 10px; }
.prev-blog img{ left: 10px; }
.prev-next:hover img{ opacity: 1; }

.comments .single-blog{ margin-bottom: 15px; padding-bottom: 15px; }
#comment-box{ width: 100%; max-width: 500px; margin-bottom: 25px; }
.comment-fields{ padding: 8px 0; margin: 0; border: 0; color: #333; border-bottom: 2px solid #333; outline: none; margin-bottom: 8px; width: 100%; font-family: monteserrat; font-size: 16px; }
textarea.comment-fields{ height: 100px; }

#comment-box input[type="submit"]{ border: 0; backface-visibility: hidden;border-radius: 0; padding: 0; margin: 0; border: 2px solid #333; color: #333; text-align: center; height: 45px; line-height: 45px; width: 125px; display: block; cursor: pointer; transition: all .5s;-webkit-transition: all .5s; -moz-transition: all .5s; background: #fff; margin-top: 8px; font-family: monteserrat; font-size: 16px; }
#comment-box input[type="submit"]:hover{ background: #333; color: #fff; }

.main .contact_reach_out h3{padding: 150px 0 0;}

.text_link{ color: #CE1E35; transition: all .5s;-webkit-transition: all .5s; -moz-transition: all .5s }
.text_link:hover{ color: #43BBE5; }

